"use client"; // Ensure this is a client component in Next.js

import React from "react";
import { Flex, IconButton, Box } from "@chakra-ui/react";
import { FaInstagram, FaSnapchatGhost, FaTiktok } from "react-icons/fa"; // Import the icons as components
import { IoLogOut } from "react-icons/io5"; 
import { useRouter } from "next/navigation"; // Use next/navigation for client-side routing

const Sidebar = () => {
  const router = useRouter();

  const navItems = [
    { icon: <FaInstagram size="24px" />, label: "Instagram", href: "/instagram" },
    { icon: <FaSnapchatGhost size="24px" />, label: "Snapchat", href: "/snapchat" },
    { icon: <FaTiktok size="24px" />, label: "TikTok", href: "/tiktok" },
  ];

  const handleLogout = () => {
    router.push("/login");
  };

  return (
    <Flex
      direction="column"
      align="center"
      bg="gray.800"
      p={4}
      width="80px"
      height="100vh"
      boxShadow="lg"
    >
      {navItems.map((item, index) => (
        <React.Fragment key={item.label}>
          <IconButton
            icon={item.icon} // Directly using the JSX component here
            aria-label={item.label}
            variant="ghost"
            mb={2}
            _hover={{ color: "teal.300" }}
            onClick={() => router.push(item.href)} // Navigation on button click
          />
          {index < navItems.length - 1 && (
            <Box height="1px" width="20px" bg="gray.400" mb={2} />
          )}
        </React.Fragment>
      ))}

      {/* Logout Icon */}
      <IconButton
        icon={<IoLogOut style={{ transform: "scaleX(-1)" }} />}
        aria-label="Logout"
        variant="ghost"
        mt="auto"
        _hover={{ color: "red.400" }}
        onClick={handleLogout} // Logout action
      />
    </Flex>
  );
};

export default Sidebar;
